import { MoreVertical } from "lucide-react";

export default function OrdersPage() {
  const orders = [
    { id: 1001, customer: "Abdelrahman Bodi", total: "$120", status: "Pending", payment: "Unpaid", date: "2025-11-30" },
    { id: 1002, customer: "Omar Ali", total: "$250", status: "Completed", payment: "Paid", date: "2025-11-29" },
    { id: 1003, customer: "Sara Mahmoud", total: "$75", status: "Cancelled", payment: "Refunded", date: "2025-11-28" },
  ];

  return (
    <div className="p-8 flex-1">

      {/* PAGE HEADER */}
      <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4 mb-10">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Orders</h1>
          <p className="text-gray-500 mt-1">Manage all orders placed in Threadix store.</p>
        </div>

        <button className="px-5 py-2.5 bg-black text-white font-medium rounded-xl hover:bg-gray-900 transition">
          + Add Order
        </button>
      </div>

      {/* SEARCH + FILTERS */}
      <div className="bg-white p-5 rounded-xl border shadow-sm mb-8 flex flex-col md:flex-row md:items-center md:justify-between gap-4">
        <input
          type="text"
          placeholder="Search orders..."
          className="w-full md:w-72 px-4 py-2.5 border rounded-xl focus:ring-2 focus:ring-black outline-none transition"
        />

        <div className="flex gap-3">
          <select className="px-4 py-2.5 border rounded-xl text-gray-700 focus:ring-2 focus:ring-black outline-none">
            <option value="">All Status</option>
            <option>Pending</option>
            <option>Completed</option>
            <option>Cancelled</option>
          </select>

          <select className="px-4 py-2.5 border rounded-xl text-gray-700 focus:ring-2 focus:ring-black outline-none">
            <option value="">Payment Status</option>
            <option>Paid</option>
            <option>Unpaid</option>
            <option>Refunded</option>
          </select>
        </div>
      </div>

      {/* ORDERS TABLE */}
      <div className="bg-white border rounded-xl shadow-sm overflow-x-auto">
        <table className="w-full min-w-[700px]">
          <thead className="bg-gray-50 border-b">
            <tr>
              <th className="text-left px-6 py-3 text-sm font-medium text-gray-600">Order ID</th>
              <th className="text-left px-6 py-3 text-sm font-medium text-gray-600">Customer</th>
              <th className="text-left px-6 py-3 text-sm font-medium text-gray-600">Total</th>
              <th className="text-left px-6 py-3 text-sm font-medium text-gray-600">Status</th>
              <th className="text-left px-6 py-3 text-sm font-medium text-gray-600">Payment</th>
              <th className="text-left px-6 py-3 text-sm font-medium text-gray-600">Date</th>
              <th className="text-right px-6 py-3 text-sm font-medium text-gray-600">Actions</th>
            </tr>
          </thead>

          <tbody>
            {orders.map((order) => (
              <tr key={order.id} className="border-b last:border-none hover:bg-gray-50 transition">
                <td className="px-6 py-4 font-semibold text-gray-900">{order.id}</td>
                <td className="px-6 py-4 text-gray-700">{order.customer}</td>
                <td className="px-6 py-4 text-gray-700">{order.total}</td>

                <td className="px-6 py-4">
                  <span
                    className={`px-2.5 py-1 text-xs font-medium rounded-lg ${
                      order.status === "Completed"
                        ? "bg-green-100 text-green-700"
                        : order.status === "Pending"
                        ? "bg-yellow-100 text-yellow-700"
                        : "bg-red-100 text-red-700"
                    }`}
                  >
                    {order.status}
                  </span>
                </td>

                <td className="px-6 py-4">
                  <span
                    className={`px-2.5 py-1 text-xs font-medium rounded-lg ${
                      order.payment === "Paid"
                        ? "bg-green-100 text-green-700"
                        : order.payment === "Unpaid"
                        ? "bg-yellow-100 text-yellow-700"
                        : "bg-red-100 text-red-700"
                    }`}
                  >
                    {order.payment}
                  </span>
                </td>

                <td className="px-6 py-4 text-gray-700">{order.date}</td>

                <td className="px-6 py-4 text-right">
                  <button className="p-2 hover:bg-gray-200 rounded-full transition">
                    <MoreVertical size={18} />
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

    </div>
  );
}
